.wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 238px;
  height: 231px;

  display: flex;
  justify-content: center;
  align-items: center;
}
.win {
  background-image: url("../../assets//winBg.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.root {
  background: #130a5d;
  border-radius: 11.2565px;
  position: relative;
  width: 150px;
  height: 151px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;

  & > * {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.title {
  font-family: "Gilroy";
  font-weight: 800;
  font-size: 12.3821px;
  line-height: 15px;

  color: #ffffff;
}
.resultText {
  font-family: "Gilroy";
  font-weight: 500;
  font-size: 9.00519px;
  line-height: 11px;

  color: #ffffff;
}
.stars {
}

.content {
  margin-top: 6px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 10px 14px;

  background: rgba(0, 0, 0, 0.5);
  border-radius: 5.62825px;
  color: #ffffff;
  width: fit-content;
  height: fit-content;
  min-height: 52px;
}

.multiplier {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 800;
  font-size: 14.6334px;
  line-height: 18px;
}
.profit {
  display: flex;
  align-items: center;
  gap: 4px;
  font-family: "Gilroy";
  font-weight: 600;
  font-size: 10.2128px;
  line-height: 13px;
}
.coinImg {
  position: static;
  width: 9px;
  height: auto;
}
