@import "sass/index";

.pawButton {
  border-radius: 4.5026px;
  padding: 4px 22px;
  flex-grow: 1;

  height: 100%;
  color: white;
  background: rgba(0, 0, 0, 0.4);

  & img,
  & svg {
    -webkit-user-select: none;
    -webkit-user-drag: none;
    user-select: none;
    user-drag: none;

    pointer-events: none;

    width: 100%;
    height: auto;
  }
  &.selected {
    background: #ff9939;
  }
}
.imgWrapper {
  background: rgba(0, 0, 0, 0.15);
  width: 22.5px;
  height: 22.5px;

  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 50%;
}

.bad {
  background: rgba(255, 255, 255, 0.07);

  &.selected {
    color: #aa333a;
    background: #dd575e !important;
  }
}
